.containerslider {
    max-width: 400px;
      height: 300px; 
       margin: 100px auto 0;
         position: relative;  
         text-align: center;
  }
  .slide {
    width: 100%;  height: 100%;  position: absolute;
    text-align: center;
 
  }
  .slide img {
    width: 100%;  height: 100%;
  }
  .btn-slide {
    background: #f1f1f1;
    border: 1px solid rgba(34, 34, 34, 0.287);
    position: absolute; 
    cursor: pointer;
  }
  .btn-slide img {
    width: 20px;  height: 20px;
  }
  .prev {
    top: 50%;  left: 0px;
  }
  .next {
    top: 50%;  right: 0px;
  }
  